import { defineStore } from "pinia";

import html2canvas from "html2canvas";

import { ref } from "vue";

export const useCaptureStore = defineStore('screenshot' , () => {
    
    const currentImage = ref("");
    const captureLink = ref("");
    const isLoading = ref(false);
    const errorCapture = ref(false);
    const isOpenModal = ref(false);

    const element = ref(HTMLElement);

    function takeScreen(elem){
        element.value = elem;
        capture();
    }

    function capture() {
        isLoading.value = true;
        errorCapture.value = false;
        currentImage.value = "";
        isOpenModal.value = true;
        window.setTimeout(() => {
            const elem = element.value;
            html2canvas(elem,
                {
                    allowTaint: true,
                    useCORS: false,
                    height: elem.offsetHeight,
                    width: elem.offsetWidth,
                }
            ).then(function (canvas) {
                isLoading.value = false;
                const dataUrl = canvas.toDataURL("image/jpeg");
                currentImage.value = dataUrl;
                captureLink.value = dataUrl;
            }).catch(() => {
                isLoading.value = false;
                errorCapture.value = true;
            });
        } , 500);
        
    }

    return {
        currentImage,
        captureLink,
        isLoading,
        errorCapture,
        element,
        isOpenModal,
        takeScreen,
        capture
    }
})