<template>
    <div class="pages">
        <div class="container-fluid px-4">
            <div class="d-flex align-items-center m-3">
                <svg xmlns="http://www.w3.org/2000/svg" width="1.6em" height="1.6em" fill="currentColor"
                    class="bi bi-file-earmark-lock2-fill text-primary" viewBox="0 0 16 16">
                    <path d="M7 7a1 1 0 0 1 2 0v1H7V7z" />
                    <path
                        d="M9.293 0H4a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h8a2 2 0 0 0 2-2V4.707A1 1 0 0 0 13.707 4L10 .293A1 1 0 0 0 9.293 0zM9.5 3.5v-2l3 3h-2a1 1 0 0 1-1-1zM10 7v1.076c.54.166 1 .597 1 1.224v2.4c0 .816-.781 1.3-1.5 1.3h-3c-.719 0-1.5-.484-1.5-1.3V9.3c0-.627.46-1.058 1-1.224V7a2 2 0 1 1 4 0z" />
                </svg>
                <span class="fs-5 lead p-2 text-secondary text-uppercase">
                    Administration
                </span>

            </div>

            <hr class="m-3" />


            <div class="d-flex align-items-center justify-content-start mx-3 text-secondary">
                <svg xmlns="http://www.w3.org/2000/svg" width="1em" height="1em" fill="currentColor" class="bi bi-filter"
                    viewBox="0 0 16 16">
                    <path
                        d="M6 10.5a.5.5 0 0 1 .5-.5h3a.5.5 0 0 1 0 1h-3a.5.5 0 0 1-.5-.5zm-2-3a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7a.5.5 0 0 1-.5-.5zm-2-3a.5.5 0 0 1 .5-.5h11a.5.5 0 0 1 0 1h-11a.5.5 0 0 1-.5-.5z" />
                </svg>
                <span class="fs-6 lead px-2">
                    Filtrer par :
                </span>
            </div>


            <div class="row mb-4 mx-3">
                <div class="col bg-white m-3 p-3 shadow">
                    <types-filter :default-types="defaultFilters.types" @onTypesChanged="typesChanged"></types-filter>
                </div>

                <div class="col bg-white m-3 p-3 shadow">
                    <stages-filter :stage-count="stageCount" :default-stages="defaultFilters.stages"
                        @onStagesChanged="stagesChanged"></stages-filter>
                </div>

                <div class="col bg-white m-3 p-3 shadow">
                    <status-filter :default-status="defaultFilters.status" @onStatusChanged="statusChanged"></status-filter>
                </div>
            </div>



            <div class="row">
                <list-cards :fetch-by="6" :show-admin="true"></list-cards>
            </div>
        </div>



    </div>
</template>


<script setup>

import { ref, reactive, watch, onMounted } from "vue";


import TypesFilter from "@/components/orbital/search/TypesFilter";
import StagesFilter from "@/components/orbital/search/StagesFilter";
import StatusFilter from "@/components/orbital/search/StatusFilter";
import ListCards from "@/components/orbital/search/ListCards";


import { useRegionsStore } from "@/stores/regions";
import { storeToRefs } from 'pinia';

const regionsStore = useRegionsStore();

const { defaultFilters, orbitalParams, firstFetch } = storeToRefs(regionsStore);
const stageCount = ref(orbitalParams.value.animationsCount);

const filters = reactive({});

onMounted(() => {
    regionsStore.fetchAuthRegions();
});

watch(filters, () => {
    regionsStore.filterRegions(filters);
});

watch(firstFetch, () => {
    regionsStore.filterRegions(filters);
});


function stagesChanged(stages) {
    filters.stages = stages;
}

function typesChanged(types) {
    filters.types = types;
}

function statusChanged(status) {
    filters.status = status;
}



</script>