import { gsap } from "gsap";


function transitionEnter(el) {
    const carousel = el.getElementsByClassName("hs-more-carousel")[0];
    const infos = el.getElementsByClassName("hs-more-infos")[0];
    const infosList = el.getElementsByClassName("infos-list");
    const dispo = el.getElementsByClassName("row-dispo")[0];
    const closeBtn = el.getElementsByClassName("close-detail")[0];
    const camera = el.getElementsByClassName("more-footer")[0];

    const timeline = gsap.timeline({
        repeat: 0 ,
    });


    timeline.set(el , {overflow : "hidden"});

    timeline.set(carousel , {
        autoAlpha : 0,
        scale : .95,
    });
    timeline.set(infos , {
        autoAlpha : 0,
        x : "100%"
    });

    timeline.set(closeBtn , {
        x : "-100%",
        autoAlpha : 0
    });

    timeline.set(camera , {
        y : "100%",
        autoAlpha : 0
    });




    timeline.fromTo(el, { autoAlpha: 0 }, {
        autoAlpha: 1,
        duration: 0.3,
    });

    timeline.to(infos , {
        autoAlpha : 1,
        x : "0",
        duration : 0.7,
        ease : "power4.out",
        onComplete : () => timeline.set(el , {overflow : "initial"})
    });

    timeline.from(infosList , {
        autoAlpha : 0,
        y : "40",
        duration : 0.5,
        stagger : 0.1,
        ease : "power4.out"
    } , "<+=0.2");

    timeline.from(dispo , {
        autoAlpha : 0,
        scale : .4,
        duration : 0.4,
        ease : "back.out(1.7)"
    } , "-=0.2");
    

    timeline.to(carousel , {
        autoAlpha : 1,
        scale : 1,
        duration : .6,
        ease : "power4.out"
    }  , "-=0.6");

    timeline.to(closeBtn , {
        autoAlpha : 1,
        x : 0,
        duration : .6,
        ease : "back.out(2)"
    });

    timeline.to(camera , {
        autoAlpha : 1,
        y : 0,
        duration : .5,
        ease : "back.out(2)"
    }, "<");
    

}

function transitionLeave(el, done) {
    const infos = el.getElementsByClassName("hs-more-infos")[0];
    const carousel = el.getElementsByClassName("hs-more-carousel")[0];

    const timeline = gsap.timeline({
        repeat: 0
    });

    timeline.set(el , {overflow : "hidden"});

    timeline.to(carousel, {
        autoAlpha: 0,
        duration: 0.4,
    });
    
    timeline.to(infos , {
        autoAlpha : 0,
        x : "100%",
        duration : 0.4,
        ease : "power4.in"
    } , "-=0.2");

    timeline.to(el, {
        autoAlpha: 0,
        duration: 0.2,
        delay : .5,
        onComplete : done
    }, "-=0.5");
}



export {transitionEnter , transitionLeave}