<template>
    <div class="row pages search-container m-0" ref="scrollElem">
        <div class="col-md-3 m-0 p-0 px-3">
            <div class="filter-product d-flex flex-column justify-content-center align-items-center">
                <span class="text-center py-4">Trouver votre appartement</span>

                <div class="slider bg-white w-100 mb-3 p-3 shadow-sm">
                    <slider-filter @onSurfaceChanged="surfaceChanged" :min="surfaceMinMax.min" :max="surfaceMinMax.max"
                        :step="0.1" :gap="2"></slider-filter>
                </div>

                <div class="types bg-white w-100 mb-3 p-3 shadow-sm">
                    <types-filter :default-types="defaultFilters.types" @onTypesChanged="typesChanged"></types-filter>
                </div>

                <div class="stages bg-white w-100 mb-3 p-3 shadow-sm">
                    <stages-filter :stage-count="stageCount" :default-stages="defaultFilters.stages"
                        @onStagesChanged="stagesChanged"></stages-filter>
                </div>

                <div class="status bg-white w-100 mb-3 p-3 shadow-sm">
                    <status-filter @onStatusChanged="statusChanged"></status-filter>
                </div>
            </div>

            <div class="to-top position-fixed bottom-0" v-if="showTopBtn">
                <button class="btn custom-btn btn-sm" @click="toTop">
                    <svg xmlns="http://www.w3.org/2000/svg" width="1.6em" height="1.6em" fill="currentColor"
                        class="bi bi-arrow-up-short" viewBox="0 0 16 16">
                        <path fill-rule="evenodd"
                            d="M8 12a.5.5 0 0 0 .5-.5V5.707l2.146 2.147a.5.5 0 0 0 .708-.708l-3-3a.5.5 0 0 0-.708 0l-3 3a.5.5 0 1 0 .708.708L7.5 5.707V11.5a.5.5 0 0 0 .5.5z" />
                    </svg>
                    <small class="text-uppercase">Filter</small>
                </button>

            </div>
        </div>

        <div class="col-md-9 m-0 p-0">
            <div class="list-product p-3 pt-4">
                <list-cards :fetch-by="6"></list-cards>
            </div>
        </div>
    </div>
</template>


<script setup>

import { useScroll } from "@vueuse/core";

import SliderFilter from "@/components/orbital/search/SliderFilter";
import TypesFilter from "@/components/orbital/search/TypesFilter";
import StagesFilter from "@/components/orbital/search/StagesFilter";
import StatusFilter from "@/components/orbital/search/StatusFilter";
import ListCards from "@/components/orbital/search/ListCards";


import gsap from "gsap";
import ScrollToPlugin from "gsap/ScrollToPlugin";


import { ref, reactive, watch, computed, onMounted } from "vue";



import { useAppDataStore } from "@/stores/appData";
import { useRegionsStore } from "@/stores/regions";
import { storeToRefs } from 'pinia';

const regionsStore = useRegionsStore();
const appDataStore = useAppDataStore();

const { surfaceMinMax } = storeToRefs(appDataStore);

const { defaultFilters, orbitalParams, firstFetch } = storeToRefs(regionsStore);


gsap.registerPlugin(ScrollToPlugin);


const stageCount = ref(orbitalParams.value.animationsCount);

const filters = reactive({});

const scrollElem = ref(null);
const scroller = useScroll(window);
const showTopBtn = computed(() => {
    return scroller.y.value > window.innerHeight * 2;
});

function surfaceChanged(sur) {
    filters.surface = sur;
}

function stagesChanged(stages) {
    filters.stages = stages;
}

function typesChanged(types) {
    filters.types = types;
}

function statusChanged(status) {
    filters.status = status;
}


watch(filters, () => {
    regionsStore.filterRegions(filters);
});

watch(firstFetch, () => {
    regionsStore.filterRegions(filters);
});


function toTop() {
    gsap.to(window, { duration: .2, scrollTo: { y: 0 } });
}

onMounted(() => {
    if (firstFetch.value) {
        regionsStore.fetchRegions();
    }
});

</script>


<style lang="scss" scoped>
.search-container {
    min-height: 100%;
    overflow: auto;
    background: rgb(227, 227, 227);
    background: linear-gradient(80deg, rgba(227, 227, 227, 1) 0%, rgba(171, 171, 171, 1) 100%);

    .filter-product {
        span {
            font-size: .9em;
        }
    }

    .list-product {
        background-color: #fff;
        min-height: 100%;
    }

}
</style>