import { defineStore } from 'pinia';

import { ref } from "vue";

import APP_DATA  from "@/assets/appData.json";

export const useAppDataStore = defineStore('appData', () => {

    const params = ref(APP_DATA.params);
    const hsParams = ref(APP_DATA.hotspots);
    const defaultFilters = ref(hsParams.value.default_filters);
    const defaultStatus = ref(defaultFilters.value.status);
    const status = ref(hsParams.value.status);
    const surfaceMinMax = ref(hsParams.value.surface);

    return {
        params,
        hsParams,
        defaultFilters,
        defaultStatus,
        status,
        surfaceMinMax
    }

});